import { MdRecordVoiceOver } from "react-icons/md";
import React, {useState} from "react";
import { v4 as uuidv4 } from 'uuid';

const testlist = [
  {
    id: 1,
    avatar: <MdRecordVoiceOver/>,
    name: "Ursula",
    message: "test"
  }, 
  {
    id: 2,
    avatar: <MdRecordVoiceOver/>,
    name: "Andy",
    message: "test23"
  },
  {
    id: 3,
    avatar: <MdRecordVoiceOver/>,
    name: "Steve",
    message: "test23"
  },
  {
    id: 4,
    avatar: <MdRecordVoiceOver/>,
    name: "Ursula",
    message: "test23"
  },
  {
    id: 5,
    avatar: <MdRecordVoiceOver/>,
    name: "Lilith",
    message: "test23"
  }, 
  {
    id: 6,
    avatar: <MdRecordVoiceOver/>,
    name: "Andy",
    message: "test23"
  },
  {
    id: 7,
    avatar: <MdRecordVoiceOver/>,
    name: "Steve",
    message: "test23"
  }
];

function GeneralRoom() {
  const [msgList, setMsgList] = React.useState(testlist);
  const [name, setName] = React.useState('');

  function handleChange(e) {
    setName(e.target.value);
  }

  function handleSubmit(e) {
    const NewM = msgList.concat({ name, id: uuidv4() });
    e.preventDefault();
    setMsgList(NewM);
    setName('');
  }

  
  return (

    <div className="borderApp">
        {testlist.map((item) =>
        <h3 key={item.id}>
        <span className="miniAvatar">{item.avatar}</span>
        {item.name}:: 
        {item.message}
      </h3>
    )}

      <form>
        <button onClick={handleSubmit}>
          <i className="fa fa-send"></i>
        </button>
        <input 
          placeholder="Make a new post here" 
          type="text"
          value={name}
          onChange={handleChange}
          />
      </form>
    </div>
  );
}

export default GeneralRoom;
